* {
  font-family: monospace;
}

body {
  background-image: url(http://i0.kym-cdn.com/photos/images/original/000/581/296/c09.jpg);
}

h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -100px;
  margin-top: -100px;
  -webkit-animation: rotate infinite 2s linear;
          animation: rotate infinite 2s linear;
  height: 80px;
  width: 200px;
  color: white;
}

@-webkit-keyframes rotate {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  15% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
  25% { -webkit-transform: rotate(14deg); transform: rotate(14deg); }
  35% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
  50% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  65% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  75% { -webkit-transform: rotate(-14deg); transform: rotate(-14deg); }
  85% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes rotate {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  15% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
  25% { -webkit-transform: rotate(14deg); transform: rotate(14deg); }
  35% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
  50% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  65% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  75% { -webkit-transform: rotate(-14deg); transform: rotate(-14deg); }
  85% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}



